:root {
    --color-light: #fffff0;
    --color-dark: #000000;
}

@font-face {
    font-family: 'Hatton-Light';
    src: url(../../assets/fonts/PPHatton-Light.woff2);
}

@font-face {
    font-family: 'Telegraf-Regular';
    src: url(../../assets/fonts/PPTelegraf-Regular.woff2);
}

@font-face {
    font-family: 'Telegraf-UltraLight';
    src: url(../../assets/fonts/PPTelegraf-UltraLight.woff2);
}

@font-face {
    font-family: 'NeueBit-Bold';
    src: url(../../assets/fonts/PPNeueBit-Bold.woff2);
}

/* Component Container */
.gallery {
    background-color: var(--color-dark);
    display: flex;
    overflow-x: scroll;
    overflow-y: hidden;
    scroll-snap-type: x mandatory;
    padding-top: 15vh;
}

.minimap {
    font-family: 'NeueBit-Bold';
    margin-left: 25px;
    color: #fffff0;
    opacity: 0.25;
    font-size: 12vw;
}

/* Individual Project Container */
.project {
    width: 100vw;
    height: 85vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    scroll-snap-align: center;
}

/* Project Text Information */
.project-info {
    padding: 2.5vw;
    width: 95vw;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.project-title {
    display: inline-block;
    color: var(--color-light);
    font-family: 'Hatton-Light';
    font-size: 8vw;
    margin-bottom: 10px;
}

.project-roles {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-bottom: 10px;
}

.role {
    color: var(--color-light);
    /* background-color: var(--color-light); */
    font-family: 'NeueBit-Bold';
    font-size: 5vw;
    border: var(--color-light) solid 1px;
    border-radius: 5px;
    padding: 5px;
    margin-right: 5px;
}

.project-bio {
    color: var(--color-light);
    font-family: 'Telegraf-Regular';
    font-size: 4vw;
    width: 90%;
    margin-bottom: 10px;
}

.project-link {
    color: var(--color-light);
    font-family: 'Telegraf-UltraLight';
    text-decoration: none;
    font-size: 4vw;
}

/* Project Image */
.project-image {
    width: 95vw;
    padding: 2.5vw;
    height: auto;
}

.thumb {
    width: 100%;
    max-height: 100%;
    border-radius: 20px;
}

@media (min-width: 720px) {
    .project {
        flex-direction: row;
    }
    /* Project Text Information */
    .project-info {
        width: 50vw;
    }
    .project-title {
        font-size: 4vw;
    }
    .minimap {
        font-size: 6vw;
    }
    .role {
        font-size: 2vw;
    }
    .project-bio {
        font-size: 1.75vw;
        width: 75%;
    }
    .project-link {
        font-size: 1.5vw;
    }
    /* Project Image */
    .project-image {
        width: 50vw;
    }
}